html, body {
  height: 100vh;
  overflow: hidden;
}

.pages {
  height: 100vh;

  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
  overflow: scroll;

  display: grid;
  grid-auto-flow: row;
}

.page {
  scroll-snap-align: start;

  height: 100vh;
  width: 100vw;
}

.navigation-dots {
  position: fixed;
  left: 24px;
  top: 50vh;
  transform: translateY(-50%);
  width: 16px;
  z-index: 9;

  display: grid;
  grid-auto-flow: row;
  grid-row-gap: 8px;

  > .dot {
    width: 16px;
    height: 16px;
    border-radius: 50%;

    background-color: #c4c4c4;

    transition: background-color 0.1s linear;

    &.default-active {
      background-color: #454545;
    }
  }
}

.bg-circles {
  width: 512px;
  height: 512px;
  border-radius: 50%;

  background-color: #eeeeee;

  padding-top: 107px;
  padding-left: 113px;
  box-sizing: border-box;

  > .inner-circle {
      width: 360px;
      height: 360px;
      border-radius: 50%;
      background-color: #F5F5F5;
  }

  &.pos-right {
      position: absolute;
      left: 100%;
      top: 100%;
      transform: translate(-50%, -50%);
  }
}