.home {
    background-color: #f7f7f7;
    position: relative;

    overflow: hidden;
}

.home > .bg-light-arc {
    position: absolute;
    height: 97%;
    width: 160%;
    bottom: 3%;
    left: -50%;
    border-bottom-right-radius: 60%;
    border-bottom-left-radius: 60%;

    background-color: #e7e7e7;
}

.home > .bg-image {
    position: absolute;
    left: 64px;
    top: 50%;
    transform: translateY(-50%);

    display: block;
    max-width: calc(0.5 * (100vw - 64px - 64px));
    max-height: 80vh;
    width: auto;
    height: auto;
}

.home > .info-container {
    position: absolute;
    right: 64px;
    top: 0;
    bottom: 0;
    width: calc(0.5 * (100vw - 64px - 64px));

    font-family: "ourFont";
    font-weight: bold;
    font-size: 10vmin;
    line-height: 17vmin;

    color: #454545;
    text-shadow: 0px 4px 7px rgba(0, 0, 0, 0.15);

    display: grid;
    grid-auto-flow: row;
    justify-content: space-around;
    align-content: center;
    direction: rtl;

    .loop-controller {
        * {
            width: 100%;
        }
    }

    .loop-item {
        display: grid;
        grid-template-columns: auto auto;
        align-items: center;
        width: 100%;

        // > .title {}

        > .image {
            height: 12vmin;
            padding-right: 3vmin;
            width: auto;
        }
    }
}
