.contact {
  background-color: #f7f7f7;
  position: relative;

  overflow: hidden;
}

.contact > .bg-light-arc {
  position: absolute;
  height: 100%;
  width: 140%;
  bottom: -20%;
  left: -30%;
  border-top-right-radius: 60%;
  border-top-left-radius: 60%;

  background-color: #e7e7e7;
}

.contact > .image {
  position: absolute;
  left: 64px;
  bottom: 64px;

  display: block;
  max-width: calc(0.5 * (100vw - 64px - 64px));
  max-height: 80vh;
  width: auto;
  height: auto;

  /* #### Mobile Phones Portrait or Landscape #### */
  @media (max-device-width: 640px){
    // background-color: lightskyblue;
    position: absolute;
    bottom: 10%;
    left: 35%;
    }

}

.contact > .info-container {
  position: absolute;
  right: 64px;
  bottom: 64px;
  width: calc(0.5 * (100vw - 64px - 64px));

  direction: rtl;
  font-family: "ourFont";
  color: #454545;

  > .header {
    font-size: 6vmin;
  }

  > .header-line {
    margin-top: 2vmin;
    height: 0.5vmin;
    width: 30vmin;
    background-color: #454545;
  }

  > .text-group {
    margin-top: 4vmin;

    display: grid;
    grid-template-columns: auto 1fr;
    grid-template-rows: 7vmin auto;

    grid-column-gap: 1.5vmin;

    align-items: center;

    > .icon {
      width: 5vmin;
      height: 5vmin;

      grid-row: 1;
      grid-column: 1;
    }

    > .title {
      font-size: 5vmin;

      grid-row: 1;
      grid-column: 2;
    }

    > .value {
      margin-top: 1vmin;

      grid-row: 2;
      grid-column: 2;

      font-size: clamp(14px, 2vw, 30px);
    }
  }

  /* #### Mobile Phones Portrait or Landscape #### */
  @media (max-device-width: 640px){
    // background-color: khaki;
    position: absolute;
    // top: 35%;
    top: 45%;
    height: 30%;
    width: 60%;
  }


}