.services {
  background-color: #f7f7f7;
  position: relative;

  overflow: hidden;
}

.services > .bg-circles {
  &.pos-left {
    position: absolute;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &.pos-right {
    position: absolute;
    left: 100%;
    top: 100%;
    transform: translate(-50%, -50%);
  }
}

//...............................................................

@keyframes shake {
  25% { transform: rotate(3.88deg); }
  50% { transform: rotate(-3.88deg); }
  75% { transform: rotate(3.88deg); }
}

.square-1 {grid-area: square-1;} 
.square-2 {grid-area: square-2;} 
.square-3 {grid-area: square-3;} 
.square-4 {grid-area: square-4;}


.part1-whenLarge {
  //Title:
  grid-area: part1-whenLarge;
  position: absolute; 
  font-family: 'ourFont';
  text-align: right;
  font-size: clamp(18px, 2vw, 30px);
  // background-color: rgba(255, 191, 0, 0.1);
  height: 50%;
  width: 85%;
  overflow: hidden;
  
    &.txt{
      top: 50%;
      right: 15%;
    }
}
.part2-whenLarge {
  //Img:
  grid-area: part2-whenLarge; 
  position: absolute;
  // background-color: rgba(222, 49, 99, 0.1);
  height: 100%;
  width: 100%;
  overflow: hidden;
  
   &.img{
    right: 25%;
    top: 15%;
   }
}
.part3-whenLarge {
  //Text:
  grid-area: part3-whenLarge; 
  position: absolute;
  text-align: right;
  font-family: 'ourFont';
  font-size: clamp(12px, 2vw, 20px);
  // background-color: rgba(64, 224, 208, 0.1);
  height: 90%;
  width: 80%;
  overflow: hidden;
  right: 20%;
  top: 10%;
}

.services {
  display: grid;
  justify-items: center;
  align-items: center;
  padding-left: 64px;
  padding-right: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
  box-sizing: border-box;
  
  > .grid-container > .back-view {
     position: absolute;
     top: 0;
     bottom: 0;
     left: 0;
     right: 0;
    //  background-color: rosybrown;
     z-index: 3;
    // opacity: 0.5;
    
    // margin: -10% -60% -10% -60%;

    margin: -30% -60% -10% -60%;


    //dadash:
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: rgba(255, 255, 255, 0.6);
  }

  > .grid-container {
    // background-color: aqua;
    position: relative;
    z-index: 2;

    // width: 100%;
    // height: calc((100vw - 64px - 24px) * 5 / 6);

    width: 90vw;
    height: 90vh;

    // background-color: pink;

  // ..............................................................
    @media screen, (max-height: calc((100vw - 64px - 24px) * 5 / 6 + 24px + 24px)) {
      height: 90vh;
      width: calc((100vh - 24px - 24px) * 6 / 5);
      display: grid;
      grid-template-areas:
      ". square-1 square-1 . square-2 square-2"
      "square-3 square-3 . square-4 square-4 .";
      grid-row-gap: 20%;
      // background-color: powderblue;
    }

    // @media all, (max-width: calc((100vh - 24px - 24px) * 6 / 5) ) {
    //   max-width:  calc((100vw - 64px - 24px) * 5 / 6 + 24px + 24px);
    //   max-height: 100vh;
    //   display: grid;
    //   grid-template-areas:
    //   ". square-1 square-1 . square-2 square-2"
    //   "square-3 square-3 . square-4 square-4 .";
    //   grid-row-gap: 20%;
    //  }
  // .........................................................

  //   @media all , (max-height: calc((100vw - 64px - 24px) * 5 / 6 + 24px + 24px)) {
  //     height: 100%;
  //     width: calc((100vh - 24px - 24px) * 6 / 5);
  //     display: grid;
  //     grid-template-areas:
  //     ". square-1 square-1 . square-2 square-2"
  //     "square-3 square-3 . square-4 square-4 .";
  //     grid-row-gap: 20%;
  //   }

  // // ..............................................................
  // //Sarah:
  //   @media all, (max-width: calc((100vh - 24px - 24px) * 6 / 5) ) {
  //     max-width:  calc((100vw - 64px - 24px) * 5 / 6 + 24px + 24px);
  //     max-height: 100vh;
  //     display: grid;
  //     grid-template-areas:
  //     ". square-1 square-1 . square-2 square-2"
  //     "square-3 square-3 . square-4 square-4 .";
  //     grid-row-gap: 20%;
  //    }
  // // .........................................................

  // background-color: salmon;

    /* #### Mobile Phones Portrait or Landscape #### */
    @media (max-device-width: 640px){
      /* some CSS here */
      // background-color: salmon;
      display: grid;
      grid-template-areas:
      "square-1 square-1"
      "square-2 square-2"
      "square-3 square-3"
      "square-4 square-4";
      grid-row-gap: 1%;
      width: 80vw;
      height: 80vh;
      // margin-top: 20%;
    }

    // display: grid;
    // grid-template-areas:
    // ". square-1 square-1 . square-2 square-2"
    // "square-3 square-3 . square-4 square-4 .";
    // grid-row-gap: 20%;


    > .square {
      position: absolute;
      width: 100%;
      height: 95%;
      background-color: #ffffff;
      box-shadow: 7px 11px 16px -2px #e5e5e5;
      border-radius: 30%;
      cursor: pointer;
      // transition: all 0.5s ease-out;

      /* #### Mobile Phones Portrait or Landscape #### */
      @media (max-device-width: 640px){
        /* some CSS here */
        // background-color: salmon;
        border-radius: 3%;
      }

      &:hover {
          border: 1px solid #000000;
          box-sizing: border-box;
          box-shadow: 7px 11px 16px -2px #dddddd;          
          animation: shake 1s;
          animation-iteration-count: 1;
        }

       > .images {
        width: 75%;
        height: 75%;
        padding-top: 10%;
        padding-left: 10%;

        /* #### Mobile Phones Portrait or Landscape #### */
        @media (max-device-width: 640px){
          // background-color: rgba(256, 125, 0, 0.2);
          width: 60%;
          height: 60%;
          // padding-top: 10%;
          padding-left: 20%;
        }

      }

      .text{
        font-family: 'ourFont';
        text-align: center;
        font-size: clamp(14px, 2vw, 20px);
      }

    }

    > .square-whenLarge {
      position: absolute;
      top: 25%;
      left: 25%;
      height: 50%;
      width: 50%;
      z-index: 4;

      background-color: #ffffff;
      box-shadow: 7px 11px 16px -2px #e5e5e5;
      border-radius: 20%;
      cursor: pointer;
      border: 1px solid #000000;
      box-sizing: border-box;

      &:hover {
          border: 2px solid #000000;
          box-sizing: border-box;
          box-shadow: 7px 11px 16px -2px #dddddd;
        }

      display: grid;
      grid-template-areas:
      "part1-whenLarge part1-whenLarge part1-whenLarge part2-whenLarge"
      "part3-whenLarge part3-whenLarge part3-whenLarge part3-whenLarge"
      "part3-whenLarge part3-whenLarge part3-whenLarge part3-whenLarge";

      /* #### Mobile Phones Portrait or Landscape #### */
      @media (max-device-width: 640px){
        // background-color: rgba(256, 125, 0, 0.2);
        width: 100%;
        left: 0;
      }

    }


    
      > .square-whenInactive {
        position: absolute;
        height: 95%;
        width: 100%;
        box-shadow: 7px 11px 16px -2px #e5e5e5;
        border-radius: 30%;
    
         > .images {
          width: 75%;
          height: 75%;
          padding-top: 10%;
          padding-left: 10%;
        }
  
        .text{
          font-family: 'ourFont';
          text-align: center;
          font-size: clamp(14px, 2vw, 20px);
        }
  
      }

  }
}


